import React from 'react';
import "./Card.css"

import Calculator from "./Calculator";

const Card = ({info}) => {

    const {titulo, cont, reverse, contId, cotizador, cardIcon } = info
    return ( 
        <div className={"card " + reverse} id={contId}>
                <div className="wraper">
                    <h2 className="title"> {titulo} </h2>
                    {cotizador ?
                    <Calculator />
                    :
                     <div className={"card-content "} >
                        <p>{cont}</p>
                        <i className="material-icons card-icon">{cardIcon}</i>
                        {/* <img src="https://i.imgur.com/U4GzC3k.jpg"></img> */}
                    </div>
                }
                </div>
            </div>
    );
}

export default Card;